.add-comment {margin:5px;}
.add-comment-btn, .submit-comment-btn, .show-replies-btn, .sort-btn {
  background-color:rgb(251, 249, 249);
  font-style: Arial; 
  min-height:2rem; 
  font-weight:bold; 
  border: 0.5px solid black;
}
.add-comment-btn:hover, .submit-comment-btn:hover, .show-replies-btn:hover, .sort-btn:hover {cursor:pointer; background-color: #7e7a7a;}
.comment-deleted {opacity:0.7;color:red;}
.comment-created-date-edited {text-decoration: line-through;color:red;}

.comment-container {
  display:flex; 
  flex-direction:row;
  min-height:200px;
  max-width:600px;
}
.comment-user{min-width:100px;}
.comment-container p {margin-top:0px;}
.comment-content{min-width:400px;}
.comment-dates {min-width:150px;}
.comment-dates p {margin:0;}
.comment-div {display:flex;flex-direction:row; max-height:30px;}
.comment-sum-interactions p {color:grey; font-size: 14px;}
.comment-item {border-bottom:1px solid grey;}
.comment-view-header {display:flex;flex-direction:row;border-bottom: 0.5px solid black;margin-left: 5px;}
.comment-view-header button, .comment-view-header select {height:30px; margin:10px;}
.sub-comments{margin-left:50px;}
.comment-interaction-container button {font-size:14px;}